.button {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  box-shadow: 1px 4px 5px rgba(0, 0, 0, 0.25);
  background: #fff;
  font-size: 3em;
  display: flex;
  align-items: center; }

.button--back {
  position: fixed;
  right: 5rem;
  top: 0.5rem;
  padding-left: 15px; }

/* --- START components --- */
.navigation-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  position: relative; }

.navigation {
  z-index: 2;
  top: 0; }
  .navigation__link {
    text-transform: uppercase; }
  @media (max-width: 50em) {
    .navigation {
      position: fixed;
      display: flex;
      flex-direction: column;
      left: -100vw;
      -webkit-transition: left 0.25s ease-in-out;
      transition: left 0.25s ease-in-out;
      -webkit-transition-delay: 0.5s;
              transition-delay: 0.5s;
      width: 100vw;
      height: 100vh;
      background: #fff; }
      .navigation__link {
        font-size: 2.5em;
        margin: 0.5em auto; } }
  @media (min-width: 50em) {
    .navigation {
      position: absolute;
      right: 0;
      font-size: 1.5em;
      margin-top: 1em; }
      .navigation__link {
        margin-right: 1rem;
        padding: 0.25rem; } }

.navigation-logo {
  width: 10em;
  position: absolute;
  margin: 1rem;
  z-index: 1; }

/* --- START small menu --- */
#navigation-checkbox {
  display: none; }
  @media (max-width: 50em) {
    #navigation-checkbox:checked ~ .navigation {
      left: 0; } }
  #navigation-checkbox:checked ~ .navigation-trigger .hamburger:before {
    -webkit-transform: translateY(10px) rotate(135deg);
            transform: translateY(10px) rotate(135deg); }
  #navigation-checkbox:checked ~ .navigation-trigger .hamburger:after {
    -webkit-transform: translateY(-10px) rotate(-135deg);
            transform: translateY(-10px) rotate(-135deg); }
  #navigation-checkbox:checked ~ .navigation-trigger .hamburger .hamburger__bar {
    -webkit-transform: scale(0);
            transform: scale(0); }
  #navigation-checkbox:checked ~ .navigation-trigger .hamburger {
    background: none;
    box-shadow: none;
    -webkit-transition-delay: 0.25s;
            transition-delay: 0.25s; }

.navigation-trigger {
  cursor: pointer;
  position: fixed;
  z-index: 3;
  right: 0.5rem;
  top: 0.5rem;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  -webkit-transition-delay: 0.75s;
          transition-delay: 0.75s; }
  .navigation-trigger:hover .hamburger {
    -webkit-transition-delay: 0s;
            transition-delay: 0s; }
    .navigation-trigger:hover .hamburger:after, .navigation-trigger:hover .hamburger:before,
    .navigation-trigger:hover .hamburger .hamburger__bar {
      background-color: #0066ff; }

.hamburger {
  width: 60px;
  height: 60px;
  padding: 12.5px;
  border-radius: 100%;
  box-shadow: 1px 4px 5px rgba(0, 0, 0, 0.25);
  background: #fff;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  -webkit-transition-delay: 0.75s;
          transition-delay: 0.75s; }
  .hamburger:after, .hamburger:before,
  .hamburger .hamburger__bar {
    background-color: #000;
    border-radius: 3px;
    content: '';
    display: block;
    height: 5px;
    margin: 5px 0;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out; }
  @media (min-width: 50em) {
    .hamburger {
      display: none; } }

/* --- END small menu --- */
.page__content {
  text-align: center;
  max-width: 1200px;
  margin: auto; }

.header__parallax:after {
  display: block;
  position: relative;
  background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#000));
  background-image: -webkit-linear-gradient(top, transparent 0%, #000 100%);
  background-image: linear-gradient(to bottom, transparent 0%, #000 100%);
  margin-top: -500px;
  height: 500px;
  width: 100%;
  content: ''; }

.header__slogan {
  position: absolute;
  width: 90vw;
  bottom: 5em;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1;
  max-width: 1200px; }
  .header__slogan h3 {
    text-align: center;
    bottom: 0;
    color: #fff;
    font-size: 1.5em;
    line-height: 1.5em; }

.blog {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: flex-start; }
  .blog .header {
    flex: 0 0 100vw; }
  .blog__first-item {
    text-align: center;
    max-width: 1200px;
    margin: auto; }
  .blog__summary {
    max-width: 600px;
    text-align: center;
    flex-basis: 100%; }
  .blog__image {
    width: 75%; }

.loading-spinner {
  font-size: 10em;
  -webkit-animation: rotate 3s linear infinite;
          animation: rotate 3s linear infinite;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

